import React, { useEffect, useRef, useState } from 'react';
import { Modal } from 'react-bootstrap';
import { db } from '../../firebase-config';
import { getDocs, collection, orderBy, query, addDoc, doc, setDoc, updateDoc, onSnapshot } from 'firebase/firestore';
import axios from 'axios';
import { useSelector } from 'react-redux';

import "./uploadmodel.css"
import Comment from './Comment';
import PinComment from './PinComment';
// import firestore from 'firebase/firestore';



const VideoModal = ({ show, handleClose, data }) => {
    const user = useSelector(state => state.auth.user);
    const [products, setProducts] = useState([])
    const [loadingProducts, setLoadingProducts] = useState(false)
    const [comments, setComments] = useState([])
    const [message, setMessage] = useState("")
    const [pinMessage, setPinMessage] = useState([])
    const [tab, setTab] = useState("products")
    // var element = document.getElementById('');
    const [pinnSecHeight, setPinsecHeight] = useState()
    const chatEndRef = useRef(null);

    const scrollToBottom = () => {
        chatEndRef.current?.scrollIntoView();
    };
    const fetchData = async (videoId) => {
        try {

            // Reference to the 'comment' collection under the 'v8760' document
            const commentRef = collection(db, 'ssv13_comments', `v${videoId}`, 'comments');

            // Query the documents in the 'comment' collection, sorted by 'createdAt' in descending order
            const q = query(commentRef, orderBy('createdAt', 'asc'));
            const snapshot = await getDocs(q);

            // Extract the data from each document
            const commentsData = snapshot.docs.map(doc => ({ id: doc.id, ...doc.data() }));



            const cms = commentsData.filter(obj => obj.isRemoved != 1);
            const objectsWithPin1 = cms.filter(obj => obj.isPinned == 1);

            setComments(cms)
            setPinMessage(objectsWithPin1)
            setTimeout(() => {
                scrollToBottom()
            }, 100);
        } catch (error) {
            console.error('Error fetching data: ', error);
        }
    };

    // const addCommentsListnerssv13 = (vId) => {
    //     // Assuming you're using Firestore for real-time updates
    //     const commentRef = db.collection(`ssv13_comments/v${vId}/comments`);

    //     // Subscribe to real-time updates for comments
    //     const unsubscribe = commentRef.onSnapshot((snapshot) => {
    //         snapshot.docChanges().forEach((change) => {
    //             if (change.type === "added") {
    //                 // Handle newly added comments
    //                 console.log("New comment added:", change.doc.data());
    //             }
    //             // You can handle other types of changes here (modified, removed)
    //         });
    //     });

    //     // Optionally, you can return the unsubscribe function if you want to stop listening to updates
    //     return unsubscribe;
    // };

    useEffect(() => {


        if (show && tab == "comments") {
            fetchData(data?.video_id);

        }

    }, [show, data.video_id, tab]);

    const fetchProducts = async () => {
        setLoadingProducts(true)
        try {
            await axios.get(`https://liveshopping.asia/getProductInfoByVideoId?brandId=${user.brand_id}&videoId=${data.video_id}`).then((res) => {
                if (res.data) {
                    setProducts(res.data.productResp)
                    // setProducts([])
                    setLoadingProducts(false)
                    scrollToBottom()
                } else {
                    setProducts([])
                    setLoadingProducts(false)
                }
            })
        } catch (error) {
            console.log(error);
            setProducts([])
            setLoadingProducts(false)
        }
    }
    useEffect(() => {
        if (show) {
            fetchProducts()
        }
    }, [show])

    const addVideoComment = async (vId, cData) => {
        try {

            const videoDocRef = doc(db, `ssv13_comments/v${vId}`);
            await setDoc(videoDocRef, {});
            const commentDocRef = await addDoc(collection(db, `ssv13_comments/v${vId}/comments`), cData);
            // if (!ssv13_commentsLoaded.includes(vId)) {
            //     // addCommentsListnerssv13(vId);
            // }
            // await fetchData(data?.video_id)
            scrollToBottom()
            subComment()

            return commentDocRef.id; // Assuming you might need the ID of the added comment
        } catch (error) {
            console.error("Error adding video comment document: ", error);
            throw error;
        }
    };

    const handleSubmit = async (e) => {
        e.preventDefault()

        try {
            let cData = {
                uName: user.first_name,
                comment: message,
                createdAt: new Date().getTime(),
                isPinned: 0,
                isRemoved: 0,
                brand_logo: user?.user_profile
            }

            await addVideoComment(data.video_id, cData)
            subComment()
            e.target.reset()
        } catch (error) {
            console.log(error);
        }
    }

    const updatePinStatus = async (comment) => {
        const commentDocRef = doc(db, 'ssv13_comments', `v${data.video_id}`, 'comments', comment.id);
        if (pinMessage.length < 4) {
            await updateDoc(commentDocRef, { isPinned: comment.isPinned == 0 ? 1 : 0 });
        }
        else {
            await updateDoc(commentDocRef, { isPinned: 1 ? 0 : 0 });
        }
        subComment()
        // await fetchData(data.video_id)


    }



    const removeComment = async (comment) => {
        const commentDocRef = doc(db, 'ssv13_comments', `v${data.video_id}`, 'comments', comment.id);
        await updateDoc(commentDocRef, { isRemoved: 1 });
        subComment()
        // await fetchData(data.video_id)
    }
    const videoId = data.video_id;
    const subComment = () => {
        // Reference to the 'comments' collection under the 'v{videoId}' document
        const commentRef = collection(db, 'ssv13_comments', `v${videoId}`, 'comments');
        // Query the documents in the 'comments' collection, sorted by 'createdAt' in ascending order
        const q = query(commentRef, orderBy('createdAt', 'asc'));

        // Setting up the onSnapshot listener for real-time updates
        const unsubscribe = onSnapshot(q, (snapshot) => {
            // Extract the data from each document
            const commentsData = snapshot.docs.map(doc => ({ id: doc.id, ...doc.data() }));

            const cms = commentsData.filter(obj => obj.isRemoved != 1);
            const objectsWithPin1 = cms.filter(obj => obj.isPinned == 1);
            setComments(cms);
            setPinMessage(objectsWithPin1);
        }, (error) => {
            console.error('Error fetching data: ', error);
        });
    }
    // useEffect(() => {

    //     // Clean up the listener on component unmount
    //     return () => unsubscribe();
    // }, [videoId]);
    console.log(tab);
    useEffect(() => {
        // setTimeout(() => {
        if (tab === "comments" && show) {
            const element = document.getElementById('pinnedcommentsection');
            console.log(element.offsetHeight);
            const pinsecHeight = element.offsetHeight
            const element2 = document.getElementById('unpinnedcommentsection')
            const element3 = document.getElementById('main-section-comments');
            // console.log();
            setPinsecHeight(element3.offsetHeight - pinsecHeight)
            if (element2) {
                element2.style.height = window.innerHeight - pinsecHeight
            }
        }
    }, [tab, pinMessage, show]);

    useEffect(() => {
        return () => {
            setTab("products")
        }
    }, [show])
    // useEffect(() => {
    //     setTimeout(() => {
    //         if (chatEndRef && comments.length > 0) {
    //             scrollToBottom();
    //         }

    //     }, 300);
    // }, []);

    return (
        <>

            <Modal show={show} className='d-grid modal_video' size='lg' onHide={handleClose} >

                <Modal.Body>
                    <div className=' d-flex m-0 p-0'  >
                        <div style={{ maxWidth: "400px", margin: "0", padding: "0" }}>
                            <video className='modal_video' controls width="600" autoPlay loop muted>
                                <source src={data.url} type="video/mp4" />
                                Your browser does not support the video tag.
                            </video>
                        </div>
                        {products.length > 0 && <div style={{ width: "400px" }} >
                            <ul className="nav nav-tabs" id="responsive-tabs" role="tablist">
                                <li className="nav-item" role="presentation">
                                    <button className="nav-link  active" id="product-tab" data-bs-toggle="tab" data-bs-target="#product" type="button" role="tab" aria-controls="video" aria-selected="true" onClick={() => setTab("products")} >Products</button>
                                </li>

                                {/* <li className="nav-item" role="presentation">
                                    <button className="nav-link " id="comments-tab" data-bs-toggle="tab" data-bs-target="#comments" type="button" role="tab" aria-controls="Reports" aria-selected="false" onClick={() => setTab("comments")}>Comments</button>
                                </li> */}
                            </ul>
                            <div className="tab-content " id="responsive-tabs-content" style={{ height: "calc(100% - 42px)" }}>
                                <div className="tab-pane fade show active h-100" id="product" role="tabpanel" aria-labelledby="product-tab">
                                    {loadingProducts ? <>
                                        <p className='text-muted mt-5 text-center'>  Loading...</p></> : <>
                                        {products.map((el, index) => {
                                            const currencySymbol = new Intl.NumberFormat(undefined, {
                                                style: "currency",
                                                currency: el.currencyName,
                                            })
                                                .formatToParts(0)
                                                .find((part) => part.type === "currency").value;

                                            return <div className='p-2 mt-1 m-0 d-flex product_card'>
                                                <img alt='productmini' src={el.image} className='rounded' style={{ height: "60px", width: "60px", border: "1px solid #aaa  " }} />
                                                <div className='ps-2'>
                                                    <h6 className='mb-0 product_title_video_model' title={el.title}>{el.title} </h6>
                                                    <p className='prouct_desc'>{currencySymbol}{parseInt(el.discountPrice)?.toLocaleString()} <span className='text-muted'><del>{currencySymbol}{parseInt(el.price)?.toLocaleString()}</del></span> </p>
                                                </div>
                                            </div>
                                        })}
                                    </>}


                                </div>
                                <div className="tab-pane fade h-100" id="comments" role="tabpanel" aria-labelledby="comments-tab">

                                    <div className="card h-100">
                                        <div className="h-100" style={{ overflow: 'hidden' }} id='main-section-comments'>
                                            {pinMessage.length > 0 ?
                                                <div className='pin display-flex flex-column'   >
                                                    {/* {pinMessage.map((el) => {
                                                        return <p className='bg-dark text-light' >{el.comment}({el.uName})  <span onClick={() => { updatePinStatus(el) }}>(unpin)</span><span onClick={() => { removeComment(el) }}>(delete)</span></p>
                                                    })} */}
                                                    <div id='pinnedcommentsection'>
                                                        {pinMessage.map((el) => {
                                                            return <PinComment el={el} updatePinStatus={updatePinStatus} removeComment={removeComment} />
                                                        })}
                                                    </div>
                                                </div> : <div id='pinnedcommentsection'></div>}

                                            {comments.length > 0 ?
                                                <>
                                                    <div class='display-flex flex-column' id='unpinnedcommentsection' style={{ overflow: "auto", height: `${pinnSecHeight}px` }}>

                                                        {comments.map((el) => {

                                                            return <Comment el={el} updatePinStatus={updatePinStatus} removeComment={removeComment} />
                                                        })}
                                                        <div ref={chatEndRef}></div>
                                                    </div>
                                                </> :
                                                <p className='text-muted text-center mt-5'>No comments</p>}

                                        </div>
                                        <div className='card-footer '>
                                            <form className='d-flex' onSubmit={(e) => { handleSubmit(e) }}>
                                                <input className='form-control mt-auto' onChange={(e) => { setMessage(e.target.value) }} placeholder='Type something here..' type='text' />
                                                <button type='submit' className='btn btn-sm btn-dark ms-2' disabled={message.length > 0 ? false : true}> Send</button>
                                            </form>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>}
                        {/* <div className='col-6 m-0 '>
                            <div className='p-2 mt-1 m-0 d-flex'>
                                <img alt='productmini' src='https://cdn.shopify.com/s/files/1/0088/4031/4931/files/39_f63c01f8-48d6-41b8-8066-1026f6f98e60.jpg?v=1702991482' className='rounded' style={{ height: "60px", width: "60px" }} />
                                <div className='ps-2'>
                                    <h6 className='mb-0'>{data.video_title} 1</h6>
                                    <p className='prouct_desc'>Here descc</p>
                                </div>
                            </div>
                            <div className='p-2 mt-1 m-0 d-flex border-top'>
                                <img alt='productmini' src='https://cdn.shopify.com/s/files/1/0088/4031/4931/files/39_f63c01f8-48d6-41b8-8066-1026f6f98e60.jpg?v=1702991482' className='rounded' style={{ height: "60px", width: "60px" }} />
                                <div className='ps-2'>
                                    <h6 className='mb-0'>{data.video_title} 2</h6>
                                    <p className='prouct_desc'>Here descc</p>
                                </div>
                            </div>
                            <div className='p-2 mt-1 m-0 d-flex border-top'>
                                <img alt='productmini' src='https://cdn.shopify.com/s/files/1/0088/4031/4931/files/39_f63c01f8-48d6-41b8-8066-1026f6f98e60.jpg?v=1702991482' className='rounded' style={{ height: "60px", width: "60px" }} />
                                <div className='ps-2'>
                                    <h6 className='mb-0'>{data.video_title} 3</h6>
                                    <p className='prouct_desc'>Here descc</p>
                                </div>
                            </div>

                        </div> */}
                    </div>
                </Modal.Body>

            </Modal>
        </>
    );
};

export default VideoModal;
