import React from 'react'
import { Col, Container, Nav, Row, Tab } from 'react-bootstrap';
const VideoSettingsTabs = () => {
    return (
        <Container style={{ height: "calc(100vh - 170px)", border: "1px solid #D1D1D2", borderRadius: "5px" }} fluid>
            <Row className='h-100 ps-2'>
                {/* <Col md={3} className="border-end"> */}
                <Tab.Container id="left-tabs-example" defaultActiveKey="video-player">
                    <Row className='h-100 pt-3 pb-3'>
                        <Col md={2} className='h-100' style={{ borderRight: "1px solid #D1D1D2" }}>
                            <Nav variant="pills" className="flex-column mt-3 video_settting_tabs">
                                <Nav.Item>
                                    <Nav.Link eventKey="playlist">Playlist</Nav.Link>
                                </Nav.Item>
                                <Nav.Item>
                                    <Nav.Link eventKey="e-commerce">E-commerce</Nav.Link>
                                </Nav.Item>
                                <Nav.Item>
                                    <Nav.Link eventKey="video-player">Video player</Nav.Link>
                                </Nav.Item>
                                <Nav.Item>
                                    <Nav.Link eventKey="product-detail-page">Product detail page</Nav.Link>
                                </Nav.Item>
                            </Nav>
                        </Col>
                        <Col md={5} className="p-3">
                            <Tab.Content>
                                <Tab.Pane eventKey="playlist">
                                    <h5 className="mt-3">Playlist Content</h5>
                                    <p>Content for Playlist tab goes here.</p>
                                </Tab.Pane>
                                <Tab.Pane eventKey="e-commerce">
                                    <h5 className="mt-3">E-commerce Content</h5>
                                    <p>Content for E-commerce tab goes here.</p>
                                </Tab.Pane>
                                <Tab.Pane eventKey="video-player">
                                    <h5 className="mt-3">Video Player Settings</h5>
                                    {/* <VideoSettings /> */}
                                </Tab.Pane>
                                <Tab.Pane eventKey="product-detail-page">
                                    <h5 className="mt-3">Product Detail Page Content</h5>
                                    <p>Content for Product Detail Page tab goes here.</p>
                                </Tab.Pane>
                            </Tab.Content>
                        </Col>
                        <Col md={5}>
                            <img src='https://i.ibb.co/HNMc5fq/Screenshot-2024-05-29-114032.png' />
                        </Col>
                    </Row>

                </Tab.Container>
                {/* </Col> */}

            </Row>
        </Container>
    )
}

export default VideoSettingsTabs