import React, { useEffect, useState } from 'react'
import "./table.css"
import { Helmet } from "react-helmet";
import Report from '../components/reports/Report';
import UploadVideo from '../components/upload-video/UploadVideo';
import VideoGallery from './VideoGallery';
// import Playlist from '../components/playlist/newplaylist';
import Playlist from './playlist';
import axios from 'axios';
import { useSelector } from 'react-redux';
import Newplaylist from '../components/playlist/Newplaylist.js';
import { PlaylistsTable } from '../components/playlist/PlaylistsTable.js';
import { useLocation, useNavigate } from 'react-router-dom';
import VideoSettingsTabs from '../components/video-settings/VideoSettingsTabs.js';

const SSVpage = ({ state, setState }) => {
    const [playlists, setPlaylists] = useState([])
    const user = useSelector(state => state.auth.user)
    const location = useLocation();
    const searchParams = new URLSearchParams(location.search);
    const createTable = searchParams.get('createTable');

    const getPlalists = async () => {
        try {
            await axios.get(`https://liveshopping.asia/getPlaylistByBrandId?brandId=${user.brand_id}`).then((res) => {
                if (res.data.playlist) {
                    setPlaylists(res.data.playlist)
                } else {
                    setPlaylists([])
                }
            })
        } catch (error) {
            console.log(error);
        }
    }

    useEffect(() => {
        getPlalists()
    }, [user])
    const navigate = useNavigate()

    const setCreateTable = (value) => {
        searchParams.set('createTable', value);
        navigate({ search: searchParams.toString() });
    };



    return (
        <>
            <Helmet>
                <title>SWIRL | Short Videos</title>
            </Helmet>
            <div>
                <div className="col">
                    <div className='d-flex'>
                        <ul className="nav nav-tabs" id="responsive-tabs" role="tablist">
                            {/* <li className="nav-item" role="presentation">
                                <button className="nav-link  active" id="video-tab" data-bs-toggle="tab" data-bs-target="#video" type="button" role="tab" aria-controls="video" aria-selected="true" onClick={() => { setCreateTable(false) }} >Video gallery </button>
                            </li> */}
                            {/* 
                            <li className="nav-item" role="presentation">
                                <button className="nav-link" id="Upload-tab" data-bs-toggle="tab" data-bs-target="#Upload" type="button" role="tab" aria-controls="Upload" aria-selected="false" disabled>Upload video</button>
    </li>*/}
                            {/* <li className="nav-item" role="presentation">
                                <button className="nav-link" id="playlist-tab" data-bs-toggle="tab" data-bs-target="#playlist" type="button" role="tab" aria-controls="playlist" aria-selected="false" onClick={() => { setCreateTable(false) }}>Playlist</button>
                            </li>
                            <li className="nav-item " role="presentation">
                                <button className="nav-link " id="Upload-tab" data-bs-toggle="tab" data-bs-target="#Upload" type="button" role="tab" aria-controls="Upload" aria-selected="false" onClick={() => { setCreateTable(false) }}>Upload video</button>
                            </li> */}
                            <li className="nav-item" role="presentation">
                                <button className="nav-link " id="Reports-tab" data-bs-toggle="tab" data-bs-target="#Reports" type="button" role="tab" aria-controls="Reports" aria-selected="false" onClick={() => { setCreateTable(false) }}>Reports</button>
                            </li>
                            {/* <li className="nav-item" role="presentation">
                                <button className="nav-link" id="Settings-tab" data-bs-toggle="tab" data-bs-target="#Settings" type="button" role="tab" aria-controls="Settings" aria-selected="false" >Settings</button>
                            </li> */}

                        </ul>
                        <div className='ms-auto d-flex align-items-center' >
                            {/* <div className="dropdown">

                                <Dropdown show={dropdownOpen} onToggle={toggleDropdown}>
                                    <Dropdown.Toggle id="dropdown-basic" className='bg-transparent text-dark border-dark'>
                                        {state[0].endDate !== null ? `${state[0].startDate.toString().slice(4, 15)} to ${state[0].endDate.toString().slice(4, 15)}` : "Filter by date"}
                                    </Dropdown.Toggle>

                                    <Dropdown.Menu>
                                        <DateRangePicker
                                            editableDateInputs={false}
                                            onChange={item => setState([item.selection])}
                                            moveRangeOnFirstSelection={true}
                                            ranges={state}
                                            months={2}
                                            direction='horizontal'
                                            maxDate={yesterday}
                                        />
                                        <div className='w-100'>
                                            <button id="dropdown-basic" className='btn btn-sm btn-primary float-end me-3' onClick={handleApply}>Done</button>
                                            <button id="dropdown-basic" className='btn btn-sm btn-outline-primary float-end me-3' onClick={() => {
                                                setState([
                                                    {
                                                        startDate: new Date(today.getTime() - 7 * 24 * 60 * 60 * 1000),
                                                        endDate: yesterday,
                                                        key: 'selection'
                                                    }
                                                ])
                                            }}>Reset</button>

                                        </div>
                                    </Dropdown.Menu>
                                </Dropdown>
                            </div> */}
                        </div>
                    </div>
                    <div className="tab-content p-3" id="responsive-tabs-content">
                        {/* <div className="tab-pane fade show" id="video" role="tabpanel" aria-labelledby="video-tab">
                            <VideoGallery />
                        </div>
                        <div className="tab-pane fade " id="Upload" role="tabpanel" aria-labelledby="Upload-tab">
                            <UploadVideo />
                        </div> */}
                        {/* <div className="tab-pane fade " id="playlist" role="tabpanel" aria-labelledby="playlist-tab">
                            {createTable == "true" ? <>
                                <Playlist />
                            </> : <>
                                {playlists.length > 0 ? <>
                                    <PlaylistsTable data={playlists} />
                                </> : <>
                                    <Newplaylist />
                                </>}
                            </>}

                         
                        </div> */}
                        <div className="tab-pane fade show active" id="Reports" role="tabpanel" aria-labelledby="Reports-tab">
                            <Report state={state} setState={setState} />
                        </div>
                        {/* <div className="tab-pane fade" id="Settings" role="tabpanel" aria-labelledby="Settings-tab">
                            <VideoSettingsTabs />
                        </div> */}

                    </div>
                </div>
            </div >

        </>
    )
}

export default SSVpage