import React, { useState } from 'react'
import { RiPushpin2Fill, RiPushpin2Line, RiPushpinFill } from "react-icons/ri";
import { FaTrash } from 'react-icons/fa';
import { IoTrashOutline } from "react-icons/io5";
import { GoDotFill } from "react-icons/go";
import moment from 'moment';
const Comment = ({ el, updatePinStatus, removeComment }) => {
    const [visible, setVisible] = useState(false)
    // Convert the timestamp to a number
    const timestampNumber = Number(el.createdAt);

    // Create a Moment.js object from the timestamp
    const momentTime = moment(timestampNumber);
    const timeFromNow = momentTime.fromNow()
    return (
        <div className={`d-flex ps-2 pe-2 pt-2 hover-target-${el.id}`} onMouseEnter={() => {
            setVisible(true)
        }}
            onMouseLeave={() => { setVisible(false) }}>
            <img style={{ height: "30px", width: "30px", borderRadius: "50%" }} src={el.brand_logo} />
            <div className='d-flex ms-2 w-100 justify-content-between'>
                <div>
                    <b>{el.uName}<GoDotFill size={10} color='#D9D9D9' /><small className='ps-1' style={{ color: "rgba(0,0,0,0.5)" }}>{timeFromNow}</small></b>
                    <p style={{ fontWeight: "lighter", color: "#515151" }}>{el.comment} </p>
                </div>
                <div className={`d-flex hidden-div-${el.id} gap-1`} style={{ visibility: visible ? "visible" : "hidden", cursor: "pointer" }}>
                    {el.isPinned == 1 ? <RiPushpin2Fill onClick={() => updatePinStatus(el)} size={21} /> : <RiPushpin2Line onClick={() => updatePinStatus(el)} size={21} />}
                    <IoTrashOutline size={18} onClick={() => removeComment(el)} color='red' />
                </div>
            </div>
        </div>
    )
}

export default Comment